import { Component, OnInit, Inject } from '@angular/core';
import { TortugaService } from "../tortuga.service";
import { DOCUMENT } from "@angular/common";
import { RegLogService } from "../reg-log.service";


@Component({
  selector: 'app-update-metrics',
  templateUrl: './update-metrics.component.html',
  styleUrls: ['./update-metrics.component.css']
})
export class UpdateMetricsComponent implements OnInit {

  constructor(@Inject(DOCUMENT) private document: Document,
   public s: TortugaService,
   public sR: RegLogService) { }

  ngOnInit() {
    this.checkUpd()
  }
  checkUpd() {
    this.s.chkUpd()
      .subscribe(w => {
        console.log(w)
        if (w == 'updated') {
          (this.document.getElementById('upd')as HTMLInputElement).disabled = true;
          this.document.getElementById('upd').innerHTML= "Updated";
          this.document.getElementById('upd').style.background = 'green';
        }
      })
  }
  hide(){
    this.document.getElementById('modal').style.display = 'none';
  }
  update() {
    this.s.upd()
      .subscribe(w => {
        console.info(w)
        this.checkUpd();
      })
  }

}
