import { Injectable } from '@angular/core';
import { HttpHeaders, HttpClient, HttpParams } from "@angular/common/http";
import { RegLogService } from "./reg-log.service";


@Injectable({
  providedIn: 'root'
})
export class TortugaService {
  server = 'https://insurebeta.herokuapp.com/';
  // server = 'http://localhost:3000/';
  constructor(private Http: HttpClient, private sR: RegLogService) { }

  chkUpd(){
    var headers = new HttpHeaders();
    headers.append('Content-Type', 'application/json');
   return this.Http.post(this.server + 'chkUpdMet', {id:this.sR.u.id}, { headers: headers })
  }

  upd(){
    console.log(this.sR.u.id)
    var headers = new HttpHeaders();
    headers.append('Content-Type', 'application/json');
   return this.Http.post(this.server + 'updateMetrics',
   {id:this.sR.u.id},
    {headers: headers})
  }
  saveToDB(a:string, c:string, tag:string){
    console.info('sent . . .')

    var headers = new HttpHeaders();
    headers.append('Content-Type', 'application/json');
   return this.Http.post(this.server + 'savePremPMT',
   {amount: a,tag:tag, phone: this.sR.u.contact, email: this.sR.u.email, uid:this.sR.u.id ,  refNo:c, status:true},
    {headers: headers})
  }
  retrivePmts(){
    var headers = new HttpHeaders();
    return this.Http.post(this.server + 'retrievePmts', {id:this.sR.u.id}, {headers: headers})
  }
  chkMthPaid(category:string){
    var headers = new HttpHeaders();
    headers.append('Content-Type', 'application/json');
   return this.Http.get(this.server + 'chkLstPaid/'+this.sR.u.id+"/"+category, { headers: headers } )
    }
  agriMetrics(f:{}){
    var headers = new HttpHeaders();
    headers.append('Content-Type', 'application/json');
   return this.Http.post(this.server+'addMachine', {uid:this.sR.u.id, f:f}, 
   { headers: headers })
  }
  deleteMachine(d:string){
    var headers = new HttpHeaders();
    headers.append('Content-Type', 'application/json');
   return this.Http.get(this.server + 'deleteMachine/'+d, { headers: headers } )
  }
  retrieveMachines(){
    var headers = new HttpHeaders();
    headers.append('Content-Type', 'application/json');
   return this.Http.get(this.server + 'retrieveMachines/'+this.sR.u.id, { headers: headers } )
  }
  payAgricPrem(){
    var headers = new HttpHeaders();
    headers.append('Content-Type', 'application/json');
   return this.Http.post(this.server+'payAgriPrem', {id:this.sR.u.id}, 
   { headers: headers })
  }
}