import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { Observable } from 'rxjs';
import { TortugaService } from "../tortuga.service";

@Component({
  selector: 'app-agri-mterics',
  templateUrl: './agri-mterics.component.html',
  styleUrls: ['./agri-mterics.component.css']
})
export class AgriMtericsComponent implements OnInit {
  j:any;
  constructor(private fb: FormBuilder, private s:TortugaService) { }
  machine = this.fb.group({
    machineName:[null, Validators.required],
    model:[null, Validators.required],
    DOP:[null, Validators.required],
    });
  ngOnInit() {
    this.rtvMachs()
  }
  delMch(d:string){
    console.log(d)
    this.s.deleteMachine(d)
    .subscribe(s=>{
      this.s.retrieveMachines()
      .subscribe(k=>{
        console.log(k)
        this.j = k;
        console.log(this.j)
      })
    })
  }
  addMachine(){
    // var r:string;
    // r.toLowerCase
    if(this.machine.get('machineName').value.slice(0,2).toLowerCase() == this.machine.get('model').value.slice(0,2).toLowerCase())
    {
      this.s.agriMetrics(this.machine.value)
      .subscribe(s=>{
        if(s){
          this.machine.reset();
          this.s.retrieveMachines()
          .subscribe(k=>{
            console.log(k)
            this.j = k;
            console.log(this.j)
          })
        }
      })
    }else{
      
    }


  }
  rtvMachs(){
    this.s.retrieveMachines()
    .subscribe(g=>{
      this.j = g
      console.log(g)
    })
  }

}
