import { Component, OnInit, Inject } from '@angular/core';
import { Router } from "@angular/router";
import { TortugaService } from "../tortuga.service";
import { DOCUMENT } from "@angular/common";
import { RegLogService } from '../reg-log.service';



@Component({
  selector: 'app-pay-premium',
  templateUrl: './pay-premium.component.html',
  styleUrls: ['./pay-premium.component.css']
})
export class PayPremiumComponent implements OnInit {
  ref: string;
  amt: number;
  j: any;
  constructor(@Inject(DOCUMENT) private document: Document,
    public r: Router,
    public s: TortugaService,
    public sR: RegLogService) { }

  vrr() {
    return Math.floor(Math.random() * 100000000).toString();
  }
  vr() {
    this.document.getElementById('ps').setAttribute('ref', this.vrr())
  }
  gotoUMT() {
    this.r.navigateByUrl('update-metric')
  }
  paymentInit() {
    console.log('Payment initialized');
  }

  checkUpd() {
    this.s.chkUpd()
      .subscribe(w => {
        console.log(w)
        if (w == 'updated') {
          console.log(w + 'modal block')
          this.document.getElementById('modal').style.display = 'none'
        }
      })
  }
  calcPremium() {
    var d = new Date(this.sR.u.DOB)
    var diff = new Date().getFullYear() - d.getFullYear()
    console.log(diff + ": " + this.amt)

    if (diff > 79) this.amt = 2000000;
    else if (diff > 59 && diff < 80) this.amt = 1800000;
    else if (diff > 39 && diff < 60) this.amt = 1500000;
    else if (diff > 19 && diff < 40) this.amt = 1200000;
    else if (diff > 9 && diff < 20) this.amt = 1000000
    else this.amt = 800000
  }

  paymentDone(ref: any) {
    var title = 'Payment for premium successfull';
    this.s.saveToDB(this.amt.toString(), this.ref, 'general')
      .subscribe(s => {

      })
  }

  paymentCancel() {
    console.log('payment failed');
  }
  chkDate() {
    this.s.chkMthPaid("general").subscribe(d => {
      if (d == "doNotPay") {
        console.log(d)
        this.document.getElementById("informer").innerHTML='Your premium for this month has not expried';
        (this.document.getElementById('paystackButton') as HTMLInputElement).disabled = true;
      }
     
    })
  }
  ngOnInit() {
    this.ref = `${Math.ceil(Math.random() * 10e13)}`
    this.calcPremium();
    this.checkUpd();
    this.chkDate();
  }
}
