import { Component, OnInit,Inject } from '@angular/core';
import { TortugaService } from '../tortuga.service';
import { RegLogService } from '../reg-log.service';
import { DOCUMENT } from "@angular/common";

@Component({
  selector: 'app-pay-agric-prem',
  templateUrl: './pay-agric-prem.component.html',
  styleUrls: ['./pay-agric-prem.component.css']
})
export class PayAgricPremComponent implements OnInit {
aa:string;a:any; ref:string;
  constructor(@Inject(DOCUMENT) private document: Document,public s:TortugaService, public sR:RegLogService) { }
  
  ngOnInit() {
  this.ref = `${Math.ceil(Math.random() * 10e13)}`;
  this.test()
  this.chkDate();
  }
  test(){
    this.s.payAgricPrem()
    .subscribe(d=>{
      this.aa = d+"00"
      this.a = d;
      console.log(d)
    }, (e)=>{
      console.info(e)
    },()=>{ 
    })
  }
  chkDate() {
    this.s.chkMthPaid("Agriculture").subscribe(d => {
      if (d == "doNotPay") {
        console.log(d)
        this.document.getElementById("informer").innerHTML='Your premium for this month has not expried';
        (this.document.getElementById('paystackButton') as HTMLInputElement).disabled = true;
      }
     
    })
  }
  paymentCancel() {
    console.log('payment failed');
  }
  paymentDone(){
    this.s.saveToDB(this.aa, this.ref, 'Agriculture')
    .subscribe(r =>{
      console.info(r + " We see you oh")
    })
  }
}
